import React, { Component } from 'react' 
import { Button, Table, Modal,message,ConfigProvider } from 'antd'
import axios from 'axios'
import moment from 'moment';
import './../index.less'
import CreateForm from './create'
import { connect } from 'react-redux' 
import { set_bread_crumb } from '../../../reducer/breadCrumb'  
import auth from '../../../auth/index'
import { menuKey } from '../../../config/menuList' 
import zhCN from 'antd/lib/locale-provider/zh_CN';

class ksEnter extends Component{


    constructor(props) {
        super(props)
        this.state = {
            isVisible: false,
            goodsId: this.props.match.params.goodsId,
            baseSelect: '',
            loadingTable: false,
            saveLoading: false
        }
    } 

    page = {
        pageNo: 1,
        pageSize: 10
    }


    requestList=()=>{
        let _this = this;
        let componentCode = auth(menuKey.PRICE, '005').authCode
        this.setState({
            loadingTable: true
        })
        axios({
            method: 'post',
            url: '/manager/busi_goods_price/listByGoodsId',  
            params: {
                pageNo: this.page.pageNo,
                pageSize: this.page.pageSize,
                goodsId: this.props.match.params.goodsId,
                componentCode: componentCode
            }    
        })
        .then((res)=>{
            if(res.status == '200'){
                let code = res.data.code;
                this.setState({
                    loadingTable: false
                })
                if(code == '00'){                
                    (res.data.data.list || []).map((item, index)=>{
                        item.key = index
                    })
                    this.setState({                    
                        dataSource:res.data.data.list,  
                        pagination: {
                            total: res.data.data.listCount,
                            current: this.page.pageNo, 
                            showTotal:()=>{
                                return `${res.data.data.listCount}`
                            },
                            onChange: (pageNo,pageSize )=>{
                                _this.page.pageNo = pageNo;
                                _this.page.pageSize = pageSize;
                                _this.requestList();
                            },
                            showSizeChanger : true,
                            onShowSizeChange: (pageNo,pageSize)=>{
                                _this.page.pageNo = pageNo;
                                _this.page.pageSize = pageSize;
                                _this.requestList();
                            },
                            showQuickJumper: {goButton:<a style={{textDecoration:"underline",paddingLeft:10}}></a>}
                        }
                    })
                }else if(code == '99'){
                    message.error('', () => {
                        this.props.history.push('/login') 
                    });
                }else if(code == '98'){
                    message.error('');
                }
            }
        })
        .catch(() => {
            message.error('')
        });
    }


    //
    requestSelect=()=>{
        let type = this.state.formType
        let componentCode = ''
        type == 'update' ? componentCode = auth(menuKey.PRICE, '002').authCode : componentCode = auth(menuKey.PRICE, '006').authCode
        axios({
            method: 'post',
            url: '/manager/busi_goods_price/add',
            params: {
                componentCode: componentCode
            }
        })
        .then((res) => {
            if(res.status == '200'){
                let code = res.data.code 
                if(code == '00'){ 
                    this.setState({  
                        isVisible: true,                    
                        baseSelect: res.data.data,                      
                    })  
                }else if(code == '99'){
                    message.error('', () => {
                        this.props.history.push('/login') 
                    });
                }else if(code == '98'){
                    message.error('');
                }
            }          
        })
        .catch(() => {
            message.error('')
        });
    }

    //
    handleEnterCreateForm=()=>{
        if(!this.state.baseSelect){
            this.requestSelect()  
        }else {
            this.setState({
                isVisible: true,
            })
        }   
        this.setState({
            title:'',
            formType: 'create',
            updateInfo: ''
        })
        
    }
    
     //
    handleSubmit = ()=> {
        //
        this.createForm.props.form.validateFields((err, values)=>{
            if(!err){
                this.setState({
                    saveLoading: true
                }) 
                let data = this.createForm.props.form.getFieldsValue()
                data.goodsId = this.state.goodsId
                data.businessTime = data.businessTime.format('YYYY-MM-DD')
                data.componentCode = auth(menuKey.PRICE, '007').authCode
                this.submit(data);
            }
        })  
    }  

    //
    submit =(data)=>{
        axios({
            method: 'post',
            url: '/manager/busi_goods_price/save',
            params: data      
        })
        .then((res) => {
            if(res.status == '200'){
                let code = res.data.code 
                if(code == '00'){ 
                    message.success('');
                    this.setState({
                        isVisible: false,
                        updateInfo: '',                       
                    })
                    this.createForm.props.form.resetFields()
                    this.requestList()                 
                }else if(code == '99'){
                    message.error('', () => {
                        this.props.history.push('/login') 
                    });
                }else if(code == '98'){
                    message.error('');
                }else {
                    message.error(res.data.msg);
                }
                this.setState({
                    saveLoading: false,
                })
            }           
        })
        .catch(() => {
            this.setState({
                saveLoading: false,
            })
            message.error('')
        });
    }

    //
    handleNoAddClick = () =>{
        this.setState({
            isVisible: false,
        })
        this.createForm.props.form.resetFields()
    }

     // 
     handleUpdate = (record)=>{
        if(!this.state.baseSelect){
            this.requestSelect()  
        }else (
            this.setState({
                isVisible: true, 
            })
        ) 
        this.setState({        
            title: '',
            updateInfo: record,
            id: record.id,
            formType: 'update'
        })
    }

     //
     handleUptSubmit = () =>{         
        this.createForm.props.form.validateFields((err, values)=>{
            if(!err){
                this.setState({
                    saveLoading: true
                }) 
                let data = this.createForm.props.form.getFieldsValue()
                data.id = this.state.id
                data.businessTime = data.businessTime.format('YYYY-MM-DD')
                data.componentCode = auth(menuKey.PRICE, '003').authCode
                this.update(data);
            }
        })
    }

     //
     update =(data)=>{
        axios({
            method: 'post',
            url: '/manager/busi_goods_price/saveUpdate',
            params: data,
        })
        .then((res) => {
            if(res.status == '200'){
                let code = res.data.code 
                if(code == '00'){ 
                    message.success('');
                    this.setState({
                        isVisible: false,
                        updateInfo: '',                      
                    })
                    this.createForm.props.form.resetFields()
                    this.requestList()                 
                }else if(code == '99'){
                    message.error('', () => {
                        this.props.history.push('/login') 
                    });
                }else if(code == '98'){
                    message.error('');
                }else {
                    message.error(res.data.msg);
                }
                this.setState({
                    saveLoading: false,
                })
            }           
        })
        .catch(() => {
            this.setState({
                saveLoading: false,
            })
            message.error('')
        });
    }

    componentDidMount () {
        this.props.dispatch(set_bread_crumb(['', '', decodeURIComponent(this.props.match.params.goodsName)+this.props.location.hash]));
        this.requestList()
    }
  
    render(){
        const columns = [
            {
                title:'',
                dataIndex:'create_user_name',
                width: 110
            },
            {
                title:'',
                dataIndex:'create_time',
                width: 110,
                render:(text, record)=>{
                    return <span>{moment(record.create_time).format('YYYY-MM-DD') }</span>
                }
            },
            {
                title: '',
                dataIndex: 'business_time',
                width: 130,
                render:(text, record)=>{
                    return <span>{moment(record.business_time).format('YYYY-MM-DD') }</span>
                }
            },
            {
                title: '',
                dataIndex: 'goods_packing_value',
                width: 80,
                render: (text, record)=>{
                    return <span style={{wordBreak:"break-all", width: 80}}>{record.goods_packing_value}</span>
                }
            },
            {
                title: '',
                dataIndex: 'certification_authority_value',
                width: 100,
                render(state){
                    return !state ?'' : state
                }
            },
            {
                title: '',
                dataIndex: 'rating_agencies_value',
                width: 80,
                render(state){
                    return !state ? '' : state
                }
            },
            {
                title: '',
                dataIndex: 'rating_fraction',
                width: 80,
                render(state){
                    return !state ? '' : state
                }
            },
            {
                title: '',
                dataIndex: 'price_type_value',
                width: 100
            },
            {
                title: '',
                dataIndex: 'goods_price',
                width: 80,
                render: (text, record)=>{
                    return <span style={{wordBreak:"break-all", width: 80}}>{record.goods_price}</span>
                }
            },
            {
                title: '',
                dataIndex: 'price_source_value',
                width: 80
            },
            {
                title: '',
                dataIndex: 'quantity',
                width: 80,
                render: (text, record)=>{
                    return <span style={{wordBreak:"break-all", width: 80}}>{record.quantity}</span>
                }
            },
            {
                title: '',
                dataIndex: 'remarks',
                width: 100,
                render: (text, record)=>{
                    return <span style={{wordBreak:"break-all", width: 100}}>{record.remarks}</span>
                }
            },
            {
                title: '', 
                width: 80,
                render: (text,record)=>{
                    return auth(menuKey.PRICE, '002').isAuth ? <a onClick={() => { this.handleUpdate(record) }}></a> : ''
                }
            }
        ]

        return (           
            <div>       
                <div className='price-enter-btn'>
                    {
                        auth(menuKey.PRICE, '006').isAuth ? <Button type="primary" onClick={this.handleEnterCreateForm}></Button> : ''
                    }
                                    
                </div>
                <div className='tb-style'>
                    <div className='price-title'></div>
                    <ConfigProvider  locale={zhCN}>
                        <Table                   
                            bordered    
                            columns={columns}
                            dataSource={this.state.dataSource}
                            pagination={this.state.pagination}
                            loading={this.state.loadingTable}
                        />
                    </ConfigProvider>                
                </div>  
                <Modal
                    title={this.state.title}
                    width={700}
                    visible={this.state.isVisible}
                    destroyOnClose= {true}
                    onCancel={()=>{                       
                        this.setState({
                            isVisible: false,
                            updateInfo: ''
                        })
                        this.createForm.props.form.resetFields()
                    }}
                    footer={
                        this.state.formType == 'update' ? 
                            ([                                                                                      
                                <Button key="submit" type="primary" loading={this.state.saveLoading} onClick={this.handleUptSubmit}></Button>,
                            ]) : 
                            ([                                                                                           
                                <Button key="submit" type="primary" loading={this.state.saveLoading} onClick={this.handleSubmit}></Button>,
                                <Button key="back" onClick={this.handleNoAddClick}></Button>,
                            ])
                    }
                >    
                    <CreateForm baseSelect={this.state.baseSelect} updateInfo={this.state.updateInfo}  wrappedComponentRef={(inst) => {this.createForm = inst }} />             
                </Modal>     
            </div>
        );
    }
}

export default connect()(ksEnter)