import React, { Component } from 'react'  
import { Form, Input, Button, Table, DatePicker, Modal, Message, ConfigProvider } from 'antd'
import { set_bread_crumb } from '../../../reducer/breadCrumb'  
import { set_current_menu } from '../../../reducer/switchMenu' 
import locale from 'antd/es/date-picker/locale/zh_CN'
import axios from 'axios'
import { connect } from 'react-redux' 
import moment from 'moment'
import 'moment/locale/zh-cn'
import ProjectForm from './form/projectForm'
import auth from '../../../auth/index'
import { menuKey } from '../../../config/menuList' 
import zhCN from 'antd/lib/locale-provider/zh_CN'

moment.locale('zh-cn');

const FormItem = Form.Item 

class Project extends Component{

    constructor() {
        super() 
        this.state = {
            visible: false,
            saveLoading: false,
            Idate: null,
            Adate: null,
            loadingTable: true,
            data: [],
            currentData: {},
            modelLoading: false 
        }
    }

    columns = [
        {
            title:'',
            key:'id',
            dataIndex:'id',
            width: 50 
        }, 
        {
            title: '',
            key: 'projectName',
            dataIndex: 'projectName',
            width: 130 
        },
        { 
            title: '',
            key: 'abbreviationName',
            dataIndex: 'abbreviationName',
            width: 100 
        },
        {
            title: '',
            key: 'issueYear',
            dataIndex: 'issueYear',
            width: 60 
        },
        {
            title: '',
            key: 'announcementDay',
            dataIndex: 'announcementDay',
            width: 60 
        },
        {
            title: '',
            key: 'issuanceDay',
            dataIndex: 'issuanceDay',
            width: 100 
        },
        {
            title: '',
            key: 'operation',
            width: 60,
            dataIndex: "operation",
            render:(text, record)=>{  
                return auth(menuKey.PROJECT, '004').isAuth ? <a onClick={() => { this.handleUpdate(record) }}></a> : ''
            }
        }
    ] 

    handleUpdate(record) {
        this.setState({
            visible: true,
            currentData: record
        })
    }

    handleSave() { 
        this.props.form.validateFields((error) => { 
            if(error) {
                return;
            }
            let data = this.props.form.getFieldsValue() 
            data['announcementDay'] = this.state.Adate
            data['issuanceDay'] = this.state.Idate
            data['componentCode'] = auth(menuKey.PROJECT, '003').authCode 
            this.setState({
                saveLoading: true
            })
            axios({
                method: 'post',
                url: '/manager/busi_dictionaries_project/save',
                params: data
            })
            .then((res) => {
                if(res.data.code == '00'){
                    let resData = res.data.data
                    resData['key'] = res.data.id
                    const newData = [...this.state.data]
                    newData.unshift(resData) 
                    this.setState({
                        data: newData
                    })
                    this.resetForm()
                    Message.success('')
                }else if(res.data.code == '99'){
                    Message.error('', () => {
                        this.props.history.push('/login') 
                    });
                }else if(res.data.code == '98'){
                    Message.error('');
                }
                this.setState({ 
                    saveLoading: false
                })
            })
            .catch(() => {
                this.setState({ 
                    saveLoading: false
                })
                Message.error('')
            }); 
        });
    }

    resetForm() {
        this.props.form.resetFields()
    }

    handleAd(date, dateString) { 
        this.setState({
            Adate: dateString
        })
    }

    handleId(date, dateString) {
        this.setState({
            Idate: dateString
        })
    }

    componentWillMount() { 
        this.props.dispatch(set_bread_crumb(['','','','']));
        this.props.dispatch(set_current_menu(['project']))
    } 

    componentDidMount() {
        axios({
            method: 'post',
            url: '/manager/busi_dictionaries_project/list',
            params: {
                componentCode: auth(menuKey.PROJECT, '001').authCode 
            } 
        })
        .then((res) => {
            if(res.data.code == '00'){
                let data = res.data.data
                data.map((item,index)=>{
                    item.key = index
                })
            }else if(res.data.code == '99'){
                Message.error('', () => {
                    this.props.history.push('/login') 
                });
            }else if(res.data.code == '98'){
                Message.error('');
            }
            this.setState({
                data: res.data.data,
                loadingTable: false
            })
        })
        .catch(() => { 
            Message.error('')
        }); 
    }

    cancelModel() {
        this.setState({
            visible: false
        })
        this.projectForm.props.form.resetFields()
    }

    saveModel() {
        this.projectForm.props.form.validateFields((error) => { 
            if(error) {
                return;
            }
            let data = {...this.state.currentData, ...this.projectForm.formData()} 
            data['componentCode'] = auth(menuKey.PROJECT, '005').authCode 
            this.setState({ 
                modelLoading: true
            })
            axios({
                method: 'post',
                url: '/manager/busi_dictionaries_project/saveUpdate',
                params: data
            })
            .then((res) => { 
                if(res.data.code == '00'){
                    this.cancelModel()
                    this.handleUpdateRow(data)
                    Message.success('')
                }else if(res.data.code == '99'){
                    Message.error('', () => {
                        this.props.history.push('/login') 
                    });
                }else if(res.data.code == '98'){
                    Message.error('');
                }
                this.setState({ 
                    modelLoading: false
                })
            })
            .catch(() => {
                this.setState({ 
                    modelLoading: false
                })
                Message.error('')
            }); 
        })
    }

    handleUpdateRow(row) {   
        let issueYear = row.issuanceDay
        issueYear = issueYear.substr(0, 4)
        row.issueYear = issueYear
        const newData = [...this.state.data];
        const index = newData.findIndex(item => row.key == item.key);
        const item = newData[index];   
        newData.splice(index, 1, {
          ...item,
          ...row,
        }); 
        this.setState({ data: newData });
    }

    render(){
        const { getFieldDecorator } = this.props.form 
        return (
            <div> 
                <div>
                    <Form id="no_inline_bottom" layout='inline'>                                 
                        <FormItem label=''>
                        {
                            getFieldDecorator('projectName', {
                                rules: [
                                    {
                                        required: true,
                                        whitespace: true,
                                        message: ''
                                    },
                                    {
                                        max: 50,
                                        message: '50'
                                    } 
                                ]
                            })(
                                <Input autoComplete="off" />
                            )
                        }                     
                        </FormItem>
                        <FormItem label=''>
                        {
                            getFieldDecorator('abbreviationName', {
                                rules: [
                                    {
                                        required: true,
                                        whitespace: true,
                                        message: ''
                                    },
                                    {
                                        max: 30,
                                        message: '30'
                                    } 
                                ]
                            })(
                                <Input autoComplete="off" />
                            )
                        }
                        </FormItem>
                        <FormItem label=''>
                            {
                                getFieldDecorator('announcementDay', { 
                                })(
                                    <DatePicker locale={locale} format={'YYYY-MM-DD'} onChange={this.handleAd.bind(this)} />
                                )
                            }                      
                        </FormItem>
                        <FormItem label=''>
                            {
                                getFieldDecorator('issuanceDay', {
                                    rules: [
                                        {
                                            required: true,
                                            message: ''
                                        } 
                                    ]
                                })(
                                    <DatePicker locale={locale} format={'YYYY-MM-DD'} onChange={this.handleId.bind(this)}/>
                                )
                            }
                        </FormItem> 
                        <FormItem>
                            {
                                auth(menuKey.PROJECT, '002').isAuth ? <Button type="primary" loading={this.state.saveLoading} onClick={this.handleSave.bind(this)}></Button> : ''
                            }
                        </FormItem>
                        <FormItem>
                            {
                                auth(menuKey.PROJECT, '002').isAuth ? <Button onClick={this.resetForm.bind(this)}></Button> : ''
                            }
                        </FormItem>
                    </Form>
                </div>
                <div className='tb-style'>
                <ConfigProvider  locale={zhCN}>
                    <Table                   
                        bordered    
                        columns={this.columns}
                        dataSource={this.state.data}  
                        pagination={false}
                        loading={this.state.loadingTable}
                    />
                </ConfigProvider>       
                </div>
                <Modal 
                    title=''
                    width={400}
                    visible={this.state.visible}
                    okText= ''
                    cancelText= '' 
                    onCancel={this.cancelModel.bind(this)}
                    footer={[
                        <Button key="back" onClick={this.cancelModel.bind(this)}>
                            
                        </Button>,
                        <Button key="submit" type="primary" loading={this.state.modelLoading} onClick={this.saveModel.bind(this)}>
                            
                        </Button>,
                    ]}
                >
                    <ProjectForm modalData={this.state.currentData} wrappedComponentRef={(form) => { this.projectForm = form }}></ProjectForm>
                </Modal>
            </div>
        );
    }
}

export default Form.create()(connect()(Project));