import React, { Component } from 'react'  
import { Form, Input, Button, Table, Modal, Message, ConfigProvider } from 'antd'
import { set_bread_crumb } from '../../../reducer/breadCrumb'  
import { set_current_menu } from '../../../reducer/switchMenu' 
import axios from 'axios'
import { connect } from 'react-redux' 
import OtherForm from './form/otherForm' 
import auth from '../../../auth/index'
import { menuKey } from '../../../config/menuList' 
import zhCN from 'antd/lib/locale-provider/zh_CN'

const FormItem = Form.Item 

class PriceFrom extends Component{

    constructor() {
        super() 
        this.state = {
            visible: false,
            saveLoading: false, 
            loadingTable: true,
            data: [],
            currentData: {},
            modelLoading: false 
        }
    }

    columns = [
        {
            title:'',
            key:'id',
            dataIndex:'id',
            width: 110 
        }, 
        {
            title: '',
            key: 'dictionariesValue',
            dataIndex: 'dictionariesValue',
            width: 180 
        },
        {
            title: '',
            key: 'operation',
            width: 60,
            dataIndex: "operation",
            render:(text, record)=>{  
                return auth(menuKey.PRICEFROM, '004').isAuth ? <a onClick={() => { this.handleUpdate(record) }}></a> : ''
            }
        }
    ] 

    handleUpdate(record) {
        this.setState({
            visible: true,
            currentData: record
        })
    }

    handleSave() { 
        this.props.form.validateFields((error) => { 
            if(error) {
                return;
            }
            let data = this.props.form.getFieldsValue()  
            data['componentCode'] = auth(menuKey.PRICEFROM, '003').authCode 
            this.setState({
                saveLoading: true
            })
            axios({
                method: 'post',
                url: '/manager/busi_dictionaries_price_source/save',
                params: data
            })
            .then((res) => {
                if(res.data.code == '00'){
                    let resData = res.data.data
                    resData['key'] = res.data.id
                    const newData = [...this.state.data]
                    newData.unshift(resData) 
                    this.setState({
                        data: newData
                    })
                    this.resetForm()
                    Message.success('')
                }else if(res.data.code == '99'){
                    Message.error('', () => {
                        this.props.history.push('/login') 
                    });
                }else if(res.data.code == '98'){
                    Message.error('');
                }
                this.setState({ 
                    saveLoading: false
                })
            })
            .catch(() => {
                this.setState({ 
                    saveLoading: false
                })
                Message.error('')
            }); 
        });
    }

    resetForm() {
        this.props.form.resetFields()
    } 

    componentWillMount() { 
        this.props.dispatch(set_bread_crumb(['','','','']));
        this.props.dispatch(set_current_menu(['priceFrom']))
    } 

    componentDidMount() {
        axios({
            method: 'post',
            url: '/manager/busi_dictionaries_price_source/list',
            params: {
                componentCode: auth(menuKey.PRICEFROM, '001').authCode 
            }  
        })
        .then((res) => {
            if(res.data.code == '00'){
                let data = res.data.data
                data.map((item,index)=>{
                    item.key = index
                })
            }else if(res.data.code == '99'){
                Message.error('', () => {
                    this.props.history.push('/login') 
                });
            }else if(res.data.code == '98'){
                Message.error('');
            }
            this.setState({
                data: res.data.data,
                loadingTable: false
            })
        })
        .catch(() => { 
            Message.error('')
        }); 
    }

    cancelModel() {
        this.setState({
            visible: false
        })
        this.otherForm.props.form.resetFields()
    }

    saveModel() {
        this.otherForm.props.form.validateFields((error) => { 
            if(error) {
                return;
            }
            let data = {...this.state.currentData, ...this.otherForm.props.form.getFieldsValue()} 
            data['componentCode'] = auth(menuKey.PRICEFROM, '005').authCode 
            this.setState({ 
                modelLoading: true
            })
            axios({
                method: 'post',
                url: '/manager/busi_dictionaries_price_source/saveUpdate',
                params: data
            })
            .then((res) => { 
                if(res.data.code == '00'){
                    this.cancelModel()
                    this.handleUpdateRow(data)
                    Message.success('')
                }else if(res.data.code == '99'){
                    Message.error('', () => {
                        this.props.history.push('/login') 
                    });
                }else if(res.data.code == '98'){
                    Message.error('');
                }
                this.setState({ 
                    modelLoading: false
                })
            })
            .catch(() => {
                this.setState({ 
                    modelLoading: false
                })
                Message.error('')
            }); 
        })
    }

    handleUpdateRow(row) {   
        const newData = [...this.state.data];
        const index = newData.findIndex(item => row.key == item.key);
        const item = newData[index];   
        newData.splice(index, 1, {
          ...item,
          ...row,
        }); 
        this.setState({ data: newData });
    }

    render(){
        const { getFieldDecorator } = this.props.form 
        return (
            <div> 
                <div>
                    <Form id="no_inline_bottom" layout='inline'>                                 
                        <FormItem label=''>
                        {
                            getFieldDecorator('dictionariesValue', {
                                rules: [
                                    {
                                        required: true,
                                        whitespace: true,
                                        message: ''
                                    },
                                    {
                                        max: 50,
                                        message: '50'
                                    } 
                                ]
                            })(
                                <Input autoComplete="off" />
                            )
                        }                     
                        </FormItem>
                        <FormItem>
                            {
                                auth(menuKey.PRICEFROM, '002').isAuth ? <Button type="primary" loading={this.state.saveLoading} onClick={this.handleSave.bind(this)}></Button>  : ''
                            }
                        </FormItem>
                        <FormItem>
                            {
                                auth(menuKey.PRICEFROM, '002').isAuth ? <Button onClick={this.resetForm.bind(this)}></Button>  : ''
                            }
                        </FormItem>
                    </Form>
                </div>
                <div className='tb-style'>
                <ConfigProvider  locale={zhCN}>
                    <Table                   
                        bordered    
                        columns={this.columns}
                        dataSource={this.state.data}  
                        pagination={false}
                        loading={this.state.loadingTable}
                    />
                </ConfigProvider>  
                </div>
                <Modal 
                    title=''
                    width={400}
                    visible={this.state.visible}
                    okText= ''
                    cancelText= '' 
                    onCancel={this.cancelModel.bind(this)}
                    footer={[
                        <Button key="back" onClick={this.cancelModel.bind(this)}>
                            
                        </Button>,
                        <Button key="submit" type="primary" loading={this.state.modelLoading} onClick={this.saveModel.bind(this)}>
                            
                        </Button>,
                    ]}
                >
                    <OtherForm modalData={this.state.currentData} fromData={{'cnName': '', 'key': 'dictionariesValue'}} wrappedComponentRef={(form) => { this.otherForm = form }}></OtherForm>
                </Modal>
            </div>
        );
    }
}

export default Form.create()(connect()(PriceFrom));